                        _icon='mdi-bullhorn'
                        _color="#3F51B5"
                        break;
                    case "/equipment/maintain":
                        _icon='mdi-hammer-wrench'
                        _color="#1DE9B6"
                        break;  
                    case "/equipment/equipmentAffirm":
                        _icon='mdi-check-bold'
                        _color="#18FFFF"
                        break;      
