                        _color="#FF9800"
                        break;     
                    case "/equipmentMessage/analyst":   // 故障分析
                        _icon='mdi-nfc-search-variant'
                        _color="#E53935"
                        break;    
                    case "/equipment/leaveOrder":   // 遗留单
                        _icon='mdi-order-alphabetical-ascending'
                        _color="#8BC34A"
                        break;                         

                    default:
                        break;
                }

                return {
                    text: o.menuName||"", 
                    icon: _icon,
                    color: _color,
                    path: o.path
                }
            })
